import {
  STAGES
} from './constants';

export function filterProjectName(projectName) {
  let reg = /\(.*?\)|.*?/;
  let sub = projectName.match(reg);
  if (sub) {
    let newName = projectName.substring(0, projectName.indexOf(sub));
    return newName;
  } else {
    return projectName;
  }
}

export function parseTime(timeString) {
  let time = timeString.match(/\d+/g);
  let day = 0, hour = 0, min = 0;
  if (time && time.length > 0) {
    day = window.parseInt(time[0]);
    hour = window.parseInt(time[1]);
    min = window.parseInt(time[2]);
  }
  return { day, hour, min };
}

export function filterProject(projectName, reg) {
  let sub = projectName.match(reg);
  if (sub) {
    let newName = projectName.substring(0, projectName.indexOf(sub));
    return newName;
  } else {
    return projectName;
  }
}

export function filterProjectStage(projectName) {
  let filteredName = null;
  for (let stage of STAGES) {
    if (projectName.indexOf(stage) != -1) {
      filteredName = projectName.substring(0, projectName.indexOf(stage));
      return filteredName;
    }
  }
  return projectName;
}
